<template>
  <div
    class="modal"
    :class="{ 'is-active': isActive }"
    v-if="examRequest != null"
  >
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Result Verification</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <patientinfo-component
          :examRequest="examRequest"
        ></patientinfo-component>
        <div style="margin-top: 15px;">
          <examresult-component
            :result="exam"
            v-for="(exam, key) in examRequest.exams"
            :key="key"
          >
          </examresult-component>
        </div>
        <div class="card" style="margin-top: 15px;">
          <div class="card-content">
            <div class="content">
              <div class="field is-grouped">
                <p class="control is-expanded">
                  <button
                    class="button is-link is-fullwidth is-medium"
                    @click="validate(examRequest)"
                  >
                    Validate
                  </button>
                </p>
                <p class="control is-expanded">
                  <button
                    class="button is-danger is-outlined is-fullwidth is-medium"
                    @click="invalidate(examRequest)"
                  >
                    Invalidate
                  </button>
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import patientInfoComponent from "@/components/PatientInfoComponent.vue";
import examResultComponent from "@/components/ExamResultComponent.vue";

export default {
  name: "ResultVerificationComponent",
  data() {
    return {
      patient: {},
      patientResults: []
    };
  },
  methods: {
    closeModal() {
      this.$store.dispatch("toggleResultVerificationModalOn", false);
      this.$store.dispatch("updateSelectedVerificationExamRequest", null);
    },
    validate(examRequest) {
      examRequest.status = "verified";
      this.$socket.emit("update-labrequest", examRequest);
      this.closeModal();
    },
    invalidate(examRequest) {
      examRequest.status = "invalidated";
      this.$socket.emit("update-labrequest", examRequest);
      this.closeModal();
    }
  },
  components: {
    "patientinfo-component": patientInfoComponent,
    "examresult-component": examResultComponent
  },
  computed: {
    isActive() {
      return this.$store.state.biologist.isResultVerificationModalOn;
    },
    examRequest() {
      return this.$store.state.biologist.selectedVerificationExamReqeust;
    }
  }
};
</script>
