<template>
  <table class="table is-fullwidth">
    <thead>
      <tr>
        <th>Time</th>
        <th>Customer</th>
        <th>Status</th>
        <th>Action</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(examRequest, key) in examRequests" :key="key">
        <td>{{ formatTime(examRequest.order_time) }}</td>
        <td class="is-capitalized">
          {{
            examRequest.patient.first_name + " " + examRequest.patient.last_name
          }}
        </td>
        <td>{{ formatStatus(examRequest.status) }}</td>
        <td>
          <button
            class="button is-link"
            @click="verifyExamRequest(examRequest)"
          >
            View
          </button>
        </td>
      </tr>
      <resultverification-component> </resultverification-component>
    </tbody>
  </table>
</template>

<script>
import resultVerificationComponent from "@/components/ResultVerificationComponent";
import { format } from "path";
export default {
  name: "ExamVerificationTableComponent",
  created() {
    this.$store.dispatch(
      "fetchBiologistLabRequests",
      this.$store.state.authentication.inMemoryToken
    );
  },
  data() {
    return {
      session: {}
    };
  },
  methods: {
    verifyExamRequest(examRequest) {
      this.$store.dispatch(
        "updateSelectedVerificationExamRequest",
        examRequest
      );
      this.$store.dispatch("toggleResultVerificationModalOn", true);
    },
    formatTime(time) {
      let date = new Date(time);
      return date.toLocaleString();
    },
    formatStatus(status) {
      switch (status) {
        case "result_registered":
          return "Result registered";
          break;
        default:
          return status;
      }
    }
  },
  computed: {
    examRequests() {
      return this.$store.getters.getPaidExamRequests;
    },
    selectedVerificationExamReqeust() {
      return this.$store.state.labtech.selectedVerificationExamReqeust;
    }
  },
  components: {
    "resultverification-component": resultVerificationComponent
  }
};
</script>
